import React from "react";
import { Link } from "react-router-dom";
import useAPI from "../hooks/useAPI";

import "./BlogList.scss";

export default function BlogList() {
  function toTimeStamp(strDate) {
    let datum = Date.parse(strDate);
    return new Date(datum).toLocaleString();
  }
  const [blogs, loading, error] = useAPI("GET", "/blogs/all");
  function BlogItem(props) {
    return (
      <li className="blogs__item">
        <Link to={"/blog/" + props.blog._id} className="blogs__link">
          <h2 className="blogs__title">{props.blog.title}</h2>
          <time dateTime={props.blog.createdAt} className="blogs__time">
            {toTimeStamp(props.blog.createdAt)}
          </time>
        </Link>
      </li>
    );
  }
  return (
    <article className="blogs">
      <ul className="blogs__list">
        {blogs.map((blog, index) => (
          <BlogItem blog={blog} key={index} />
        ))}
      </ul>
      <span className="system _loading">{loading && "Loading..."}</span>
      <span className="system _error">{error && "ERROR"}</span>
      {!loading && !error && !blogs.length && (
        <span className="title">Be the first who create a blog!</span>
      )}
    </article>
  );
}
