import axios from "axios";
import { useEffect, useState } from "react";

export default function useAPI(method, url, object) {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);

  const [data, setData] = useState([]);
  useEffect(() => {
    setError(false);
    setLoading(true);
    let cancel;
    axios({
      method: method,
      url: url,
      ...object,
      cancelToken: new axios.CancelToken((c) => (cancel = c)),
    })
      .then((res) => {
        setLoading(false);
        console.log(res);
        setData(res.data);
      })
      .catch((err) => {
        if (axios.isCancel(err)) return;
        setLoading(false);
        setError(true);
        console.log(err);
      });
    return () => cancel();
  }, [method, url, object]);

  return [data, loading, error];
}
