import axios from "axios";
import React, { useState } from "react";
import { useHistory } from "react-router-dom";

import "./BlogCreate.scss";

export default function BlogCreate() {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const history = useHistory();

  function sendForm(e) {
    e.preventDefault();
    setLoading(true);
    setError(false);

    const data = Object.fromEntries(new FormData(e.target).entries());
    axios
      .post("/blogs/create/", data)
      .then((res) => {
        setLoading(false);
        history.push("/blog/" + res.data._id);
      })
      .catch((err) => {
        setError(true);
        setLoading(false);
      });
  }
  return (
    <section className="create">
      <h1 className="create__title title">Add a new blog</h1>
      <form onSubmit={sendForm} className="create__form container">
        <label className="create__label">
          Blog title:
          <br />
          <input name="title" type="text" className="create__field" required />
        </label>
        <label className="create__label _wide">
          Blog body:
          <br />
          <textarea name="body" className="create__area" required></textarea>
        </label>
        <label className="create__label">
          Author:
          <br />
          <input name="author" type="text" className="create__field" required />
        </label>
        <button className="btn create__btn" type="submit">
          {error && "ERROR "}
          {loading && "Loading..."}
          {!error && !loading && "Create"}
        </button>
      </form>
    </section>
  );
}
