import axios from "axios";
import React, { useState } from "react";
import { useParams } from "react-router";
import { useHistory } from "react-router-dom";
import useAPI from "../hooks/useAPI";

import "./BlogFile.scss";

export default function BlogFile() {
  const { id } = useParams();
  const history = useHistory();
  const [blog, blogLoading, blogError] = useAPI("GET", `/blog/${id}`);

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);

  if (!blog) return <p>NOT FOUND</p>;

  function deleteBlog() {
    setLoading(true);
    setError(false);
    axios({ method: "DELETE", url: "/blogs/delete/", data: { _id: id } })
      .then((res) => {
        setLoading(false);
        history.push("/");
      })
      .catch((err) => {
        console.log(err);
        setError(true);
        setLoading(false);
      });
  }
  function toTimeStamp(strDate) {
    let datum = Date.parse(strDate);
    return new Date(datum).toLocaleString();
  }
  return (
    <article className="blog">
      <header className="blog__header">
        <h1 className="blog__title title">{blog.title}</h1>
        <time dateTime={blog.createdAt} className="blog__time">
          {toTimeStamp(blog.createdAt)}
        </time>
      </header>
      <p className="blog__text">{blog.body}</p>
      <address className="blog__author">{blog.author}</address>
      {!blogError && !blogLoading && (
        <button onClick={deleteBlog} className="btn blog__btn">
          {error && "ERROR "}
          {loading && "Loading..."}
          {!error && !loading && "Delete blog"}
        </button>
      )}
      <span className="system _loading">{blogLoading && "Loading..."}</span>
      <span className="system _loading">{blogError && "ERROR"}</span>
    </article>
  );
}
